                <span  class="fr base mouseDefault"  @click="goNone()"></span>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    name:'accountSet',
    data(){
        return {
            routerList:["setCash","setCash","setCash","setCash","setCash","setCash"]
        }
    },
    methods:{
        goTo(index){
            // this.$router.push({name: this.routerList[index]})
            layer.msg("...")
        },
        goNone(){
            layer.msg("...")
        }
    }
}
</script>
<style lang="scss" scoped>
$navBack:#181b2a;	
$base:#5697f4;
$line:#303b4b;
$fColor2:#637085;
.account-main {
    padding-left: 34px;
    padding-right: 34px;
    padding-top: 34px;
    .bar-bottom {
      width: 320px;
      height: 8px;
      border-radius: 4px;
      background-color: $navBack;
      margin: 22px 0 12px 0;
      overflow: hidden;
      .bar-top {
        background-color: $base;
        height: 100%;
      }
    }
    ul {
      border-top: 1px solid $line;
      color: $fColor2;
      font-size: 14px;
      img {
        width: 16px;
        vertical-align: middle;
      }
      li {
        border-bottom: 1px solid $line;
        line-height: 72px;
        position: relative;
        p {
          position: absolute;
          left: 300px;
          top: 0;
        }
      }
    }
  }
</style>





