<template>
    <div class="currency">
        <div class="title color"></div>
        <div class="content f14 fColor1">
            <p class="ft24 color">BTC</p>
            <p class="fColor2 abstract ft14"></p>
            <p class="mt15 ft14 des">
                BitcoinJNB200913P2P2100102140P2P
            </p>
        </div>
    </div>
</template>
<script>
export default {
    name:"currency"
}
</script>
<style scoped>
.currency{background-color: #fff;border:1px solid #ccc}
.title{height: 48px;line-height: 46px;padding: 0 40px 0 30px;}
.content{padding: 15px 30px 60px;}
.ft24{font-size: 24px}
.abstract{padding:15px 0;border-bottom: 1px solid #ccc;}
.des{
    color: #333;
}
</style>




