<template>
    <div class="wrap">
        <h1 class="title tc bold mb10"></h1>
        <p class="ft16 tc">10<span class="red">1ETH</span></p>
        <div class="main">
           <div class="inp_wrap clear" v-for="(item,index) in list" :key="index">
               <p class="fl lebal ft14">{{item.lebal_name}}</p>
               <input class="fl inp ft14" :type="item.type" :name="item.name" value="" :placeholder="item.placeholder" />
           </div>
           <div class="img">
              <img src="../assets/images/yzm.png" />
           </div>
           <div class="inp_wrap clear">
               <p class="fl lebal ft14"></p>
               <input class="fl inp ft14" type="text" name="yanzhengma" value="" placeholder="" />
           </div>
           <div class="submit_btn">
               
           </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            list:[
                {lebal_name:'Email',placeholder:'gate.io',type:'text',name:'emails'},
                {lebal_name:'',placeholder:'Email',type:'email',name:'team_emails'},
                {lebal_name:'',placeholder:'Bitcoin',type:'text',name:'English_name'},
                {lebal_name:'',placeholder:' ',type:'text',name:'chinese_name'},
                {lebal_name:'',placeholder:'BTC',type:'text',name:'symbol'},
                {lebal_name:'ICO',placeholder:'YYYY/MM/DD',type:'text',name:'ico_date'},
                {lebal_name:'',placeholder:'YYYY/MM/DD',type:'text',name:'circul_date'},
                {lebal_name:'',placeholder:'ETH,QTUM,NEO,XLM,BTS',type:'text',name:'emails'},
                {lebal_name:'',placeholder:' ',type:'text',name:'address'},
                {lebal_name:'',placeholder:'Bitcoin8',type:'text',name:'tofixed'},
                {lebal_name:'',placeholder:'',type:'text',name:'offical_website'},
                {lebal_name:'',placeholder:'',type:'text',name:'white_website'},
                {lebal_name:'',placeholder:'',type:'text',name:'broswer'},
                {lebal_name:'Logo',placeholder:'',type:'text',name:'img_src'},
                {lebal_name:'Twitter',placeholder:'',type:'text',name:'twitter_src'},
                {lebal_name:'Telegram',placeholder:'',type:'text',name:'telegram_src'},
                {lebal_name:'',placeholder:'',type:'text',name:'chinese_des'},
                {lebal_name:'',placeholder:'',type:'text',name:'English_des'},
                {lebal_name:'',placeholder:'',type:'text',name:'currency_account'},
                {lebal_name:'',placeholder:'',type:'text',name:'currency_circle'},
                {lebal_name:'',placeholder:'ICO',type:'text',name:'proportion'},
                {lebal_name:'',placeholder:'presale,private sale,public sale',type:'text',name:'price'},
                {lebal_name:'',placeholder:'',type:'text',name:'trade_terrace'},
                {lebal_name:'',placeholder:'',type:'text',name:'other_msg'},
            ]
        }
    }
}
</script>
<style scoped>
    .wrap{
        padding-top: 20px;
    }
    .title{
        font-size: 28px;
    }
    .main{
        width: 1200px;
        margin: 0 auto;
        margin-top: 10px;
        border-top: 1px solid #C8DAE2;
        padding: 30px 0;
    }
    .inp_wrap{
        width: 800px;
        margin: 0 auto;
        margin-bottom: 10px;
        display: flex;
        justify-content: center;
        border: 1px solid #eee;
    }
    .lebal{
        width: 200px;
        background: #eee;
        color: #333;
        padding: 10px;
        border-right: 1px solid #ccc;
        text-align: right;
    }
    .inp{
        width: 600px;
         padding: 10px;
    }
    .img{
        width: 800px;
        margin: 0 auto;
    }
    .submit_btn{
        width: 800px;
        margin: 0 auto;
        padding: 10px 0;
        font-size: 16px;
        color: #fff;
        text-align: center;
        background: #d45858;
    }
    .submit_btn:hover{
        background: #ca4141;
        cursor: pointer;
    }
</style>



